<template>
    <div>
        <div class="routeHead">
            <div class="frameTag"></div>
            <span>{{title}}</span>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

.routeHead
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    padding-top 10px
    padding-left 2px
    span
        margin-left 10px
        color $color-theme-blue
        font-weight $font-weight-level_5
        font-size $font-size-level_4
    .frameTag
        width 4px
        height 20px
        background-color $color-theme-blue 
</style>





