<template>
    <div class="infoRow">
      <div>{{left}}</div>
      <div>{{right}}</div>
    </div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  props: {
    left: {
      type: String,
      default: ''
    },
    right: {
      type: String,
      default: ''
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

.infoRow
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    height 20px
    margin-top 5px
    div
        font-size $font-size-level_2
    div:first-of-type
        width 100px
        text-align left
        padding-left 16px
        color $color-theme-gray_2
    div:last-of-type
        flex-grow 2
        text-align right
        padding-right 10px
        color black
</style>


